const metaData = {
  title: [
    <span>Do Urban Parks Promote </span>,
    <br />,
    <span>Racial Diversity?</span>,
  ],
  subtitle: "Evidence from New York City",
  id: "nyc-parks-diversity",
  year: 2020,
  authors: [],
  abstract:
    "While policymakers and urban planners often praise public space for promoting racial diversity, empirically it remains unclear whether these places play a role in creating diverse social environments for the city's residents. Focusing on parks in New York City as the exemplar of modern public space, I estimate the causal relationship between access to parks and individually experienced diversity. To do so, I introduce a measure of racial diversity that captures one's level of exposure to diverse others in places visited on a daily basis, utilizing a novel dataset featuring individual GPS tracking data for more than 60 thousand Twitter users in the New York metro area. My empirical strategy relies on obtaining a time-varying measure of access to parks that incorporates information about ongoing construction and repair works across the city. The results show that additional 10 acres of parkland within the 5km radius from home increase individual chances of encounters with other racial/ethnic groups by 1%. I also provide evidence to suggest that park accessibility affects the diversity of white and black residents differently: for parks located closer to home, the effect appears to be more pronounced for whites than blacks, indicating that parks in the majority-white neighborhoods are able to attract a broader range of visitors compared to the local parks in black neighborhoods.",
  pdflink: parksDiversityFile,
  tags: [
    { name: "Job Market Paper", color: "oranLight" },
    { name: "Working Paper" },
  ],
  open: 1,
};


